.rent-section {
   width: 100%;
   background-color: white;
   display: block;
   padding: 100px 0px 40px 0px;
}

.important-section {
   box-sizing: border-box;
   margin: 0 auto;
   max-width: 1140px;
   margin: 0 auto;

 
   .head-text {
   width: 100%;
    color: #716D6E;
    margin: 0px 0px 45px 0px;
    font-family: "Lato", Sans-serif;
    font-size: 33px;
    font-weight: 900;
    line-height: 1.1em;
   }

}

.important-div {
   margin: 0 auto;
   display: flex;
   height: 400px;
   max-width: 1140px;

   position: relative;
   padding: 15px;
  

}

.info-section {
   animation-name: slidein;
   display: flex;
   max-width: 862px;
   min-width: 662px;
   height: auto;
}

.right-div {
   margin: 0 auto;
   width: 275px;


   button {
      width: 100%;
      height: 60px;
      border-radius: 0px;
      background-color: #e0dede;
      border: none;
      margin-bottom: 1px;
      position: relative;
      transition: .2s;
      font-family: "Lato", Sans-serif;
      color: #716D6E;
      text-align: left;
      font-size: 20px;
      font-weight: bold;
      cursor: pointer;
   }
   
   button:hover {
      background-color: #d1cdce;
   }

   button:focus {
      background-color: #ffe599;
      transition: 0.2s;
   }
}

.center-div {
   width: 571px;
   margin: 0 auto;
   padding: 40px;

   img {
      width: 100%;
      height: auto;
   }
}

.left-div {
 
   width: 287px;
   display: inline-table;

   .price {  
    width: 287px;
    background-color: rgba(255,191,0,.4);
    height: 53px;
    line-height: 53px;
    font-size: 20px;
    font-weight: bold;
    color: #727070;
   }
    

   table{
   width: 100%;
    max-width: 100%;
    margin-bottom: 1rem;
    background-color: transparent;
    border: 1px solid #e7e7e7;
    border-collapse: separate;
    border-spacing: 0;
    border-top: 0;

    tr {
      display: table-row;
      vertical-align: inherit;
      border-color: inherit;

      td {
   padding: 8px;
    line-height: 1.42857143;
    vertical-align: top;
    border-top: 0;
    border-left: 0;
    text-align: left;
    padding: 0.5rem;
    border-top: 1px solid #dee2e6;
    border-left: 1px solid #dee2e6;

      }
    }
   }

   .cars-btn {
      width: 255px;
      height: 35px;
      background-color: #ffbf00;
      color: white;
      display: inline-block;
      text-align: center;
      font-size: 24px;
      font-weight: 700;
      padding: 6px 15px;
      box-shadow: 6px 6px 0 #efe9e9;
      transition: .2s;
      border-radius: 0;
      border: none;
      cursor: pointer;

   }
}


@media (max-width: 1025px) {
  .left-div {
     display: none;
  }

  .center-div {
     width: 100%;
     height: 150%;
  }
}
