.book-section {
   width: 100%;
   height: 367px;
   background-image: url(https://demo.xpeedstudio.com/carrental/home-v4/wp-content/uploads/sites/5/2020/06/cta_car_action.png);
   background-position: top right;
   background-repeat: no-repeat;
   background-size: auto;
   // background-size: cover;
   // opacity: 0.5;
   transition: background 0.3s, border-radius 0.3s, opacity 0.3s;
   background-color: #2D2D2D;
}

.book-container {
   width: 100%;
   height: 100%;
   background-color: rgba(27, 27, 27, 0.5);
}

.booking-div {
   // border: 1px solid black;
   max-width: 1140px;
   height: 202px;
   margin: 0 auto;
   position: relative;
   top: 80px;
   text-align: center;
   display: flex;
}

.booking-info-left {
   
   width: 60%;
   height: 202px;
   // border: 1px solid black;

   h2 {
      width: 615px;
      color: #FFFFFF;
      margin: 0px 0px 10px 0px;
      font-family: "Poppins", Sans-serif;
      font-size: 52px;
      font-weight: bold;
      line-height: 62px;
      letter-spacing: -1.36px;
      text-align: left;
   }

   p {
      width: 615px;
      color: #FFFFFF;
    font-family: "Rubik", Sans-serif;
    font-size: 24px;
    font-weight: 400;
    line-height: 28px;
    letter-spacing: -0.72px;
    text-align: left;
   
   }
}

.booking-btn-right {
   width: 40%;
   height: 202px;
   // border: 1px solid black;
   text-align: right;
   line-height: 202px;
   position: relative;

   .booking-btn {
      width: 172px;
      height: 60px;
      margin-right: 10px;
      background-image: linear-gradient(to right,#ff4c30,#ff6830,#fff,#f3806f);
     background-size: 300% 100%;
     border: none;
     border-radius: 5px;
     color: #fff;
     padding: 19px 27px 19px 27px;
    font-family: "Rubik", Sans-serif;
    font-size: 18px;
    font-weight: 500;
    text-transform: capitalize;
    line-height: 22px;
    letter-spacing: -0.36px;
    background-color: transparent;
    background-image: linear-gradient(100deg, #FF4C30 0%, #FF6830 100%);
    border-style: none;
    border-radius: 3px 3px 3px 3px;
    box-shadow: 0px 10px 15px 0px rgb(255 83 48 / 35%);
    cursor: pointer;
   }
}
