.single-head {
   padding: 5px 0 5px 370px; 
   position: relative;
   background-color: #ffbf00;
   // height: 40px;
   
   span {
      font-size: 30px;
      color: white;
      font-family: lato, Sans-serif;
      font-weight: 700;
   }
}

.single-import-div {
  border-top: 3px solid lightgray;
}

.single-section {

   display: block;
   position: relative;
   padding: 100px;
   padding-top: 50px;
}

.single-div {
   margin: 0 auto;
   width: auto;
   max-width: 1140px;
   height: auto;
   position: relative;
   padding: 15px;
   display: flex;
}

.single-left {
   width: 70%;
   height: 100%;
}

.single-left-name {

   h1 {
      margin: 0;
      margin-bottom: 14px;
      line-height: 1.2;
      font-size: 36px;
      font-weight: 700;
   }
}


.single-left-image {
   width: 100%;
   height: 502px;
   border: 1px solid gray;
   border-radius: 5px;

   img {
      width: 100%;
      height: 100%;
   }
}

.single-info-down {
   top: 40px;
   height: auto;
   // border: 1px solid  black;
   display: block;
   position: relative;
   border-radius: 5px;
}

.single-info-div {
   height: 60px;
   // border: 1px solid black;
   margin: 0;
   background-color: #232628;
   border-left: 4px solid #232628;
   border-right: 4px solid #232628;
   overflow: visible;

   button {
      padding-left: 15px;
      padding-right: 15px;
      width: auto;
      background: none;
      color: #ffffff;
      border: none;
      margin-right: 25px;
      height: 100%;
      font-size: 12px;
      font-weight: bold;
      border-radius: 5px;
      cursor: pointer;
   }

   button:focus {
      background-color: #fff;
      color: #232628;
      transition: 0.5s;
      border-top: 5px solid #ffbf00;
      border-radius: 2px;
      cursor: pointer;
   }
}

.single-info-card {
   padding-top: 15px;
   padding-bottom: 0px;
   // border: 1px solid black;
   text-align: left;
}

.single-first-card-1 {
   margin: 0 0 14px;
   display: block;
   margin-block-start: 1em;
   margin-block-end: 1em;
   margin-inline-start: 0px;
   margin-inline-end: 0px;
   font-family: "Lato", Sans-serif;
   color: #333;
   font-size: 18px;
}

.single-first-card-2 {
   margin: 0 0 14px;
