}

.single-first-card-2-image {
   width: 40px;
   height: 40px;
   display: flex;
   

   img {
      width: 100%;
      height: auto;
   }
   p {
      line-height: 0px;
      padding-left: 15px;
      font-size: 22px;
      color: #ffbf00;
   }
