.bpb {
   position: relative;
   top: 5px;
   width: 100%;
   


   .single-p {
      margin-top: -40px;
       text-align: right;
       margin-right: 5px;
   }
}











.single-right {
   width: 30%;
   height: 100%;
   padding-left: 30px;
}

.single-right-div {

   height: 100%;
   display: block;
}

.single-right-up-block {
   width: 100%;
   height: auto;
   max-height: 327px;
   border: 1px solid gray;
   border-radius: 5px;

}

.single-up-price {
   
   width: 100%;
   height: 70px;
   background-color: #ffbf00;
   line-height: 70px;
   text-align: center;

   h1 {
      position: relative;
      top: -1px;
      left: 1px;
      color: #fff;
      vertical-align: middle;
      margin: 0;
      font-size: 35px;
      font-weight: 700;
   }
}

.single-nds {
   // height: 40px;
   padding: 10px 10px;
   // background-color: #a0a1a1;
   background-color: #232628;
    font-size: 13px;
    text-align: center;
    color: #fff;
}

.single-tr {
   margin: 0 auto;
   height: auto;
   padding: 20px 26px 13px 23px;
   // margin-bottom: 26px;
   background-color: #f0f2f5;

   table {
      padding-left: 25px;
      width: 300px;
 
      margin-bottom: 0;
      table-layout: fixed;
      background-color: transparent;

      hr {
         width: 250px;
      }
      
      .t-label {
         padding-right: 3px;
         text-transform: uppercase;
         color: #888;
         font-size: 12px;
      }

      .t-value h6 {
         padding-left: 3px;
         margin-bottom: 0;
         font-weight: 700;
         font-size: 14px;
         line-height: 18px;
         padding: 11px 0 6px;
      }
   }
}

.single-right-down-block {
   margin-top: 30px;
   width: 100%;
   max-height: 205px;
   border: 1px solid gray;
   border-radius: 5px;
   background-color: #f0f2f5;

}

.single-right-book-btn {
   height: 50px;
   text-align: center;
   display: block;
    text-align: center;

   button {
      width: 100%;
      height: 100%;
   background-color: #ffbf00;
   font-size: 14px;
   line-height: 14px;
   font-weight: 700;
   letter-spacing: 0.3px;
   text-transform: uppercase;
   color: #fff;
   border-radius: 5px;
   border: none;
   }

   button:hover {
      color: white !important;
      background-color: #ffd452;
   }
}

