    font-size: 24px;
    font-weight: 900;
    color: #ffffff;
    background-color: #FFBF00;
    border-style: solid;
    border-width: 1px 1px 1px 1px;
    border-color: #FFFFFF;
    box-shadow: 0px 0px 0px 0px rgb(0 0 0 / 50%)
  }
  button:hover {
    background-color: #716D6E;
    transition: 2s;
  }
}


