}

.time-datee {
  color: #ffbf00;
  font-size: 1rem;
  font-weight: 700;
}



@keyframes section-left {
  0% {
    -webkit-transform: translate3d(-300%,0,0);
    transform: translate3d(-300%,0,0);
}

58% {
    -webkit-transform: translate3d(27px,0,0);
    transform: translate3d(27px,0,0);
}
73% {
    -webkit-transform: translate3d(-12px,0,0);
    transform: translate3d(-12px,0,0);
}
88% {
    -webkit-transform: translate3d(8px,0,0);
    transform: translate3d(8px,0,0);
}
}

.section-right {
width: 661px;
height: 530px;
margin: 0 auto;
position: relative;
display: inline-block;
overflow: hidden;
text-align: center;
animation: section-right;
animation-duration: 1.25s;
}

@keyframes section-right {
  0% {
    opacity: 0;
    transform: translate3d(0,100%,0);
}

100% {
    opacity: 1;
    transform: none;
}
}


article {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0;
  transition: all 0.3s linear;
}

article.activeSlide {
  opacity: 1;
  transform: translateX(0);
}
article.lastSlide {
  transform: translateX(-100%);
}
article.nextSlide {
  transform: translateX(100%);
}

.person-img {
  max-width: 100%;
  height: auto;
}

.text {
  padding-left: 30px;
    font-family: "Lato", Sans-serif;
    font-size: 29px;
    font-weight: 900;
    text-transform: uppercase;
    text-align: left;
    line-height: 0.1em;
    color: #FFFFFF;
}

.title {
  padding-left: 30px;
  font-family: "Lato", Sans-serif;
    font-size: 24px;
    text-align: left;
    font-weight: 400;
    line-height: 1.1em;
    color: #716D6E;
}

.prev,
.next {
  transform: translateY(500px);
  position: relative; 
  width: 1.25rem;
  height: 1.25rem;
  background: none;
  font-size: 1rem;
  cursor: pointer;
  transition: all 0.3s linear;
  border: none;
}
.prev {
  left: 0;
}
.next {
  right: 0;
} 



@media (min-width: 1025px) {
  .text {
    max-width: 45em;
  }
  .prev,
  .next {
    width: 2rem;
    height: 2rem;
    font-size: 1.5rem;
  }
}

.section-left {
  display: inline-block;
  max-width: 1050px;
  text-align: center;
  background-color: white ;
  overflow: hidden;
  box-shadow: 0 9px 0 0 #fff, 0 -9px 0 0 #fff, 7px 0 15px -4px rgb(0 0 0 / 30%), -7px 0 15px -4px rgb(0 0 0 / 30%);
  animation-duration: 1s;
  animation-name: section-left;
  padding: 45px 35px 30px 15px;

}

@media (max-width: 767px) {
  .section-container {
    height: auto;
    display: block;
}

.section-left {
  width: 100%;
  height: 500px;
  margin-bottom: 10px;
}

.section-right {
  margin-top: 10px;
  width: 100%;
  height: 500px;

}

.text {
  max-width: 45em;
}
.prev,
.next {
  width: 2rem;
  height: 2rem;
  font-size: 1.5rem;
}

article {
  width: 100%;
  height: 100%;
  text-align: center;
  justify-content: center;
  margin: 0 auto;
}

}

.slider-btn {
  width: 400px;
  height: 55px;
  font-size: 24px;
  line-height: 1.75em;
  cursor: pointer;
  background-color: #ffbf00;
    color: #fff;
    display: inline-block;
    font-weight: 900;
    padding: 6px 15px;
    box-shadow: 6px 6px 0 #efe9e9;
    transition: .2s;
    border-radius: 0;
    border: none;
    font-family: var( --e-global-typography-primary-font-family ), Sans-serif;
}

.slider-btn:hover {
  background-color: #716d6e;
  transition: .2s;
}
