.swiper-section {
  width: 100%;
  height: 832px;
  position: relative;
  background-color: #f9f9f9;
}

.swiper-div {
  width: 1140px;
  height: 615px;

  margin: 0 auto;
  position: relative;
  top: 70px;
}

.swiper-info-top {
         width: 700px;
         height: 207px;
         margin: 0 auto;
         text-align: center;
         position: relative;
         top: 20px;
   

         h3 {
          color: #010103;
          font-family: "Rubik", Sans-serif;
          font-size: 22px;
          font-weight: 600;
          line-height: 27px;
          letter-spacing: -0.66px;
          margin-bottom: 10px;
         }

         h2 {
          color: #010103;
          margin: 0px 0px 30px 0px;
          font-family: "Poppins", Sans-serif;
          font-size: 44px;
          font-weight: bold;
          line-height: 52px;
          letter-spacing: -1.32px;
          margin-bottom: 30px;
         }

         p {
          color: #817F91;
          font-family: "Rubik", Sans-serif;
          font-size: 16px;
          font-weight: 400;
          line-height: 25px;
          letter-spacing: -0.24px;
          margin-top: 10px;
          margin-bottom: 10px;
         }
}
  

.swiper-slider {
  width: 1120px;
  height: 388px;
  margin: 0 auto;

  position: relative;
  
}

.rec-arrow-left, .rec-arrow-right {
  display: none;
}

  .swiper-block {
    background-color: white;
    box-shadow: 0px 20px 10px 0px rgb(206, 199, 199);
    width: 540px;
    height: 308px;
    margin-right: 10px;
    margin-left: 10px;

    .swiper-block-div {
      width: 430px;
      height: 200px;
      position: relative;
      margin: 0 auto;
    
      top: 40px;

      .block-text-top {
        width: 430px;
        height: 130px;
    

        p {
          font-family: "Poppins", Sans-serif;
          font-size: 24px;
          font-weight: bold;
          line-height: 36px;
          letter-spacing: -0.48px;
          color: #010103;
          margin: 0px 0px 20px 0px;
        }
      }
    }

    .block-text-down {
      display: flex;
     

      .rec-carousel {
        width: 1120px;
        height: 310px;
      
      }

     
      // background-color: aquamarine;
    

      .block-down-img {
        height: 70px;
        width: 70px;
        height: auto;

        img {
          border-radius: 50%;
          width: 100%;
          height: auto;
        }
      }

      .block-down-name {
        display: block;
        -webkit-tap-highlight-color: transparent;
    
        width: 252px;
        display: inline;
        box-sizing: border-box;
        margin-left: 20px;
        padding-top: 17px;

        strong {
          display: flex;
          color: #010103;
          font-family: "Poppins", Sans-serif;
          font-size: 18px;
          font-weight: 600;
          line-height: 18px;
          letter-spacing: -0.36px;
          margin-bottom: 0px;
        }

        span {
          font-family: "Rubik", Sans-serif;
          font-size: 16px;
          font-weight: 400;
          letter-spacing: -0.24px;
        }
      }

    }
  }

  .block-down-icon {
    display: flex;
    width: 70px;
      height: 70px;
      justify-content: center;
      align-items: center;

    i {
      font-size: 32px;
      color: #FF4C30; 
     
    }
  }


  rec-slider-container {
    position: relative;
  }

  .rec-item-wrapper {
    // background-color: wheat;
    width: 300px;
    height: 310px;
    margin: 0 auto;
    position: relative;
  }

  .rec-carousel-wrapper {
    padding-top: 25px;
    position: relative;
    height: 310px;
    top: 40px;

    -webkit-tap-highlight-color: transparent;
   
  }

  .rec-pagination {
    position: relative;
    top: 50px;
  }

