.footer-section {
   width: 100%;
   position: relative;
   background-color: #DBDBDB;
 
   padding: 40px 0px 50px 0px;
}

.footer-div { 
   margin: 0 auto;
   max-width: 1140px;


}

.item-div {
   width: 100%;
   height: 20px;
  justify-content: center;

   display: flex;
   font-family: "Lato", Sans-serif;
   font-size: 18px;
   font-weight: bold;
   margin-bottom: 20px;
}

.footer-item {
   list-style: none;
   margin-right: 25px;
   font-family: "Lato", Sans-serif;
    font-size: 18px;
    font-weight: bold;
    text-transform: capitalize;
    text-decoration: underline;
    color: #4d4c4c;
    text-decoration: none;

    a {
      color: #716D6E;
      transition: 0.5s;
    }
    a:hover {
      color: #ffba00;
      transition: 0.5s;
   }
}

.footertext-div {
   width: 100%;
   height: 32px;
   text-align: center;

   margin-bottom: 20px;

   p {
      color: #716D6E;
      margin: 0px 0px 2px 0px;
      font-family: "Lato", Sans-serif;
      font-size: 18px;
      font-weight: 400;
      line-height: 1.7em;
   }
}

.footericon-div {
   text-align: center;
   
   .fa-footer {
      
      display: inline-block;
      text-align: center;
      text-decoration: none;
      border-radius: 0% 0% 0% 0%;
      padding: 6px 7px 6px 7px;
      font-size: 30px;
      font-weight: 400;
      width: 30px;
      height: 23px;
      line-height: 20px;
      color: #716D6E;
      transition: 0.5s;
   }

   .fa-footer:hover {
      color: #ffba00;
      transition: 0.5s;
   }
}