.container {
   width: 100%;
   max-width: 1140px;
   text-align: center;
   justify-content: center;
   display: block;
   margin-right: auto;
   margin-left: auto;
   padding: 115px 0 80px;
   height: auto;
   position: relative;
   box-sizing: border-box;
   // border: 1px solid black;

.text-container {
   width: 100%;
   text-align: center;
   animation: text-container ;
   animation-duration: 1.25s;

   @keyframes text-container {
      0% {
         opacity: 0;
         transform: translate3d(0,100%,0);
     }
     
     100% {
         opacity: 1;
         transform: none;
     }
   }

   h1 {
      color: #716d6e;
      margin: 0 0 15px;
      font-family: lato,Sans-serif;
      font-size: 46px;
      font-weight: 900;
   }

   h4 {
      color: #716d6e;
      font-family: lato,Sans-serif;
      font-size: 20px;
      font-weight: 700;
      line-height: 1.4em;
      margin: 0 0 15px;
   }

   .hr {
      color: #ffc414;
      width: 12%;
      height: 5px;
      background-color: #ffc414;
      border: none;
   }
}

.block-container {
   margin-top: 50px;
}


.block-container-top, .block-container-bottom {
   width: 100%;
   max-width: 1140px;
   margin-right: auto;
   margin-left: auto;
    position: relative;
   //  border: 1px solid black;
    display: flex;
    overflow: hidden;
    box-sizing: border-box;
}

