.block-margin {
  margin: 20px;
  width: 50%;
   padding: 10px;
   z-index: 2;
   background-color: #ededed;
   border-style: solid;
   border-width: 1px;
   border: 1px solid #f5f5f5;
   cursor: pointer;
}


.block{
   padding: 10px;

   #block-icons {
      background-color: #ffc107;
      color: white;
      position: relative;
      top: 9px;
      font-weight: bold;
      font-size: 28px;
      padding: 5px;
   }
  
   

   h2 {
      margin: 5px 0 30px;
      padding: 0;
      color: #716d6e;
      font-family: lato,Sans-serif;
      font-size: 28px;
      font-weight: 900;
   }

   h2:hover {
      color: #ffc107;
      transition: 0.5s;
   }

   p {
      color: #716d6e;
      font-family: lato,Sans-serif;
      font-size: 18px;
      font-weight: 400;
      line-height: 1.7em;
   }
 }
}

//  Customer Service 

@media (max-width: 767px) {

   .text-container {
      display: block;
      height: auto;
   }
 

    .block {
       width: 500px;
       height: auto;
    }
 

    .block-container-top, .block-container-bottom {
   
      height: auto;
      // margin-right: auto;
      // margin-left: auto;
       position: relative;
       display: inline-block;
      
       .block-margin {
         width: 100%;
        
      }
   }

  
   
 }