.call-section {
   width: 100%;
   max-width: 3776px;
   height: 176px;
   background-image: url(https://demo.xpeedstudio.com/carrental/home-v4/wp-content/uploads/sites/5/2020/07/cta-bg-image.png);
    background-position: center center;
    background-repeat: no-repeat;
    background-size: cover;
}

.call-section-overlay {
   height: 100%;
   width: 100%;
   // position: absolute;
   background: rgba(44, 40, 40, 0.5);
   -webkit-tap-highlight-color: transparent;
   transition: background 0.3s, border-radius 0.3s, opacity 0.3s;
}

.call-section-div {
   width: 1140px;
   height: 116px;
   position: relative;
   margin: 0 auto;
  
   top: 30px;
   display: flex;
   
}

.call-text-left {
   text-align: left;
   width: 50%;
   height: auto;
  

   h2 {
      width: 400px;
      color: #FFFFFF;
    margin: 0px 0px 0px 0px;
    font-family: "Poppins", Sans-serif;
    font-size: 36px;
    font-weight: 700;
    line-height: 48px;
    letter-spacing: -0.72px;
   }
}

.call-phone-right {
  padding-top: 20px;
  
   width: 50%;
   height: auto;
  

   .call-phone-div {
      position: relative;
      left: 150px;
      width: 400px;
      height: auto;

   }

 

   span {
      font-family: "Rubik", Sans-serif;
    font-size: 18px;
    font-weight: 400;
    line-height: 26px;
    color: white;


    i {
       color: #FFFFFF;
       font-size: 18px;
       font-weight: bold;
    }
   }

   h2 {
      color: #FF5430;
   
      margin: 0px 0px 0px 0px;
      font-family: "Rubik", Sans-serif;
      font-size: 36px;
      font-weight: 700;
      line-height: 43px;
      letter-spacing: -0.56px;
    }
}