.head-carousel {
   width: 100%;
   height: 410px;
   background-image: url(https://demo.xpeedstudio.com/carrental/wp-content/themes/carrental/assets/images/banner/bredcumbs-1.png);
   background-repeat: no-repeat; 
   background-size: 100% 100%;
   background-size: cover;

   .container {
      max-width: 100%;
      height: 100%;
      background: rgba(255, 255, 255, 0.5);
      padding: 150px 50px 150px 50px;
   }

   .text-container {
      text-align: left;
      // border: 1px solid black;
      width: 100%;
      max-width: 1140px;
      margin: 0 auto;
      height: auto;
      padding-right: 15px;
      padding-left: 15px;
      
      h1 {
         color: #010103;
         margin: 0 0 15px;
         font-family: lato, Sans-serif;
         font-size: 35px;
         font-weight: 700;
      }

      h3 {
         color: #010103;
         text-transform: capitalize;
         font-family: lato, Sans-serif;
         font-size: 17px;
         font-weight: 600;
         line-height: 18px;
      }
   }

   
}

