.contact-section {
   width: 100%;

   padding: 85px 0px 30px 0px;
  
}

.contact-flex {
   display: flex;
   max-width: 1140px;
   margin-left: auto;
   margin-right: auto;
 
}




.contact-div {
   margin: 0 auto;
   max-width: 721px;
 
   padding: 15px;
   position: relative;
 }

 .text-block {
    max-width: 721px;
    height: 140px;



    h1 {
      color: #716D6E;
      margin: 0px 0px 25px 0px;
      font-family: "Lato", Sans-serif;
      font-size: 36px;
      font-weight: 900;
      line-height: 1.1em;
    }

    h3 {
      color: #716D6E;
      font-family: "Lato", Sans-serif;
      font-size: 18px;
      font-weight: 400;
      line-height: 1.7em;
      margin: 0px 0px 0px 0px;
    }

    p {
      color: #716D6E;
      font-size: 18px;
      font-weight: 900;
      line-height: 1.7em;
      margin: 0px 0px 15px 0px;

      b {
         color: #ffbf00;
      }
    }
 }

 .input-formm {
    display: block;
    max-width: 721px;
    height: 436px;
   //  border: 1px solid black;

    .inp-name {   
      max-width: 50%;
      width: 345px;
      font-size: 18px;
      font-weight: 500;
      height: 50px;
       float: left;
       height: 50px;
       margin-top: 20px;
       border: none;
       color: #716D6E;
       background-color: #DBDBDB;
       
    }

    .inp-lastname{
      max-width: 50%;
      width: 345px;
      font-size: 18px;
      font-weight: 500;
      height: 50px;
      float: right;
      margin-top: 20px;
      border: none;
       color: #716D6E;
       background-color: #DBDBDB;
    }

    .inp-telephone {
      max-width: 50%;
      width: 345px;
      font-size: 18px;
      font-weight: 500;
      height: 50px;
      float: left;
      margin-top: 20px;
      border: none;
       color: #716D6E;
       background-color: #DBDBDB;
    }

    .inp-email {
      max-width: 50%;
      width: 345px;
      font-size: 18px;
      font-weight: 500;
      height: 50px;
      float: right;
      margin-top: 20px;
      border: none;
       color: #716D6E;
       background-color: #DBDBDB;
    }

    .inp-message {
      font-size: 18px;
      font-weight: 500;
       border: none;
       color: #716D6E;
       background-color: #DBDBDB;
      margin-top: 25px;
       width: 100%;
       max-width: 717px;
       height: 196px;
       resize: vertical;
    }
    
  

   
 }

 .inp-btn {
    margin-top: 10px;
    margin-left: 30%;
    width: 721px;
    height: 46px;
 }

 .inp-btn button {
   padding: 5px 15px 5px 15px;
   font-family: "Lato", Sans-serif;
   font-size: 24px;
   font-weight: 900;
   line-height: 36px;
   color: #ffffff;
   border-color: #ffffff;
   background-color: #ffbf00;
   border-radius: 0px 0px 0px 0px;
   box-shadow: 6px 6px 0px 0px #efe9e9;
 }

 .customers-block {
   max-width: 419px;
   padding: 15px;
   position: relative;
   display: block;

    h3 {
      color: #716D6E;
      margin: 0px 0px 0px 0px;
      font-family: "Lato", Sans-serif;
      font-size: 18px;
      font-weight: bold;
      line-height: 1.1em;
    }
 }

 .customer1 {
      background-color: #EDEDED;
      width: 100%;
      height: 115px;
      display: flex;
      margin-top: 17px;
 }

 .customer-div {
    
    display: flex;
    padding: 10px 49px 0px 20px;
    border-style: solid;
    border-width: 0px 0px 10px 0px;
    border-color: #FFBF00;
    border-radius: 0px 0px 0px 0px;

 }

 .customer1-image {
    width: 91px;
    height: 84px;
    border: 1px solid #FFBF00;

    img {
       width: 100%;
       height: 100%;
    }
 }

 .customer1-info {
   padding-left: 10px;
   width: 175px;
   height: 84px;

   h3 {
      padding: 0px 0px 0px 0px;
      color: #716D6E;
      font-family: "Lato", Sans-serif;
      font-size: 24px;
      font-weight: bold;
   }

   p {
      font-family: "Lato", Sans-serif;
    font-size: 14px;
    font-weight: 500;
    line-height: -2em;

    b {
      color: #ffbf00;
    }
   }
 }

 .change-text {
     color: #716D6E;
      margin: 17px 0px 0px 0px;
      font-family: "Lato", Sans-serif;
      font-size: 18px;
      font-weight: bold;
      line-height: 1.1em;
 }

 @media (max-width: 1140px) {
     .customers-block {
        display: none;
     }
 }
 
 