.about-section {
    width: 100%;
    height: 738px;
    position: relative;
}

.about-div {
    width: 1140px;
    height: 548px;
    margin-right: auto;
    margin-left: auto;
    position: relative;
    top: 100px;
 
    display: flex;
    box-sizing: border-box;
}

.about-image{
    width: 456px;
    height: 548px;
   
          
    .about-image-div{
        width: 436px;
        height: 450px;
        margin: 0 auto;
        position: relative;
        top: 40px;
        display: block;

        .image-padding {
            padding: 20px;
        }
        

        img {
            width: 100%;
            height: auto;
            border: none;
        }

    }
}

.section-youtube-icon {
    position: absolute;
    z-index: 1;
    background-color: red;
    width: 120px;
    height: 120px;
    right: -39px;
    bottom: -65px;
    border-radius: 7.5px;
    border: none; 
}


.about-info {
    width: 684px;
    height: 548px;

}

.about-info-div {
    width: 524px;
    height: 499px;
    position: relative;
    margin: 25px 90px 0px 70px;
    padding: 10px;
}

.about-info-top {
    position: relative;
    width: 504px;
    height: 367px;
    margin-bottom: 20px;
    border-style: solid;
    border-width: 0px 0px 1px 0px;
    border-color: #F1F1F1;
    border-radius: 0px 0px 0px 0px;


    h3 {
        color: #010103;
        font-family: "Rubik", Sans-serif;
        font-size: 22px;
        font-weight: 500;
        line-height: 27px;
        letter-spacing: -0.66px;
    }

    h2 {
        color: #010103;
        margin: 0px 0px 30px 0px;
        font-family: "Poppins", Sans-serif;
        font-size: 44px;
        font-weight: bold;
        line-height: 52px;
         letter-spacing: -1.32px;
         margin-bottom: 20px;
    }

    p {
    color: #817F91;
    font-family: "Rubik", Sans-serif;
    font-size: 16px;
    font-weight: 400;
    line-height: 25px;
    letter-spacing: -0.24px;
    margin-top: 10px;
   }
}


.about-info-bottom {

    position: relative;
    width: 504px;
    height: 126px;
    display: flex;
}

.about-spans {
    width: 33.33%;
 
    max-width: 167px;
    height: 126px;
}

.spans {
    display: block;
}

.spans-image-div {
    width: 167px;
    text-align: left;
}

.spans-info-div {
    width: 100%;
    max-width: 167px;
    height: 63px;
  
    display: flex;

    span{
    color: #010103;
    font-family: "Poppins", Sans-serif;
    align-items: center;
    text-align: center;
    font-size: 52px;
    font-weight: 700;
    color: #333;
    }

    h3 {
       
        max-width: 20px;
        color: #817F91;
        font-family: "Rubik", Sans-serif;
        font-size: 16px;
        font-weight: 700;
        line-height: 18px;
        letter-spacing: -0.24px;
        margin-left: 20px;
        
    }
}
