.carousel-wrapper {
   width: 100%;
   height: 579px;


   .comment-div {

       max-width: 1140px;
       height: 319px;
       justify-content: center;
       margin: 0 auto;
      
       margin-top: 140px;
   }

   .stars {
  
      position: relative;
    z-index: 6;
    font-family: Lato,sans-serif;
    font-size: 22px;


    font-weight: 600;
    line-height: 1.625;
    color: #716d6e;

    .fa-star {
       margin: 7px;
    }

    .fan {
       font-size: 30px;
    }
   }

   .carousel .carousel-status {
      // position: absolute;
      // top: 0;
      // right: 0;
      // padding: 5px;
      // font-size: 10px;
      // text-shadow: 1px 1px 1px rgb(0 0 0 / 90%);
      // color: #fff;
      display: none;
  }



   .text {
     width: 855px;
     height: 163px;
    margin: 0 auto;
   align-items: center;
margin-top: 20px;
    font-family: "Lato", Sans-serif;

   p {
      text-align: center;
      font-size: 19px;
      font-weight: 200;
      line-height: 1.7em;
      color: #716D6E;
    margin: 0px 0px 20px 0px;
   
   }
   }

   .person-name {
      color: #FFBF00;
      font-size: 18px;
      font-weight: 900;
      margin-bottom: 0px;
   }

}

.carousel .control-dots .dot {
   transition: opacity .25s ease-in;
   opacity: .3;
   filter: alpha(opacity=30);
   background: #fff;
   border: 6px solid black;
   border-radius: 50%;
   width: 8px;
   height: 8px;
   cursor: pointer;
   display: inline-block;
   margin: 0 8px;
}