.input-section {
   display: flex;
   width: 100%;
   background-color: #ffbf00;
   position: relative;
   padding: 100px 0px 60px 0px;
}

.input-container {
   width: 100%;
   height: 60px;
   display: flex;
   margin-right: auto;
   margin-left: auto;
   position: relative;
}

.input-text {
   width: 43%;
   height: 100%;


   .input-text-div {
      width: 100%;
      padding: 0px 0px 0px 30px;
   }

   h2 {
      color: #FFFFFF;
      margin: 0px 0px 6px 0px;
      font-family: "Lato", Sans-serif;
      font-size: 30px;
      font-weight: 900;
      text-transform: uppercase;
      line-height: 1.1em;
   }

   h3 {
      color: #716D6E;
      font-family: "Lato", Sans-serif;
      font-size: 18px;
      font-weight: 400;
      text-transform: uppercase;
      line-height: 1.1em;
      margin: 0px 0px 0px 0px;
   }

}

.input-center {  
   width: 36.67%;
   height: 100%;
   position: relative;
   padding: 2px 35px 0px 0px;
   align-content: center;
    align-items: center;

      .input-form {
      width: 100%;
      max-width: 571px;
      min-width: 200px;
      height: 54px;
      font-size: 14px;
      font-weight: 400;
      border-style: solid;
      border-color: rgba(2, 1, 1, 0);
      border-width: 2px 2px 2px 2px;
      border-color: #B8B6B6;
      font-family: "Lato", Sans-serif;
      font-size: 14px;
   }



      button {
         width: 100%;
         min-width: 50px;
         width: 71px;
         height: 59px;
         font-family: "Lato", Sans-serif;
         font-size: 15px;
         font-weight: 900;
         border-style: solid;
         border-color: #B8B6B6;
         color: #716D6E;
         background-color: #FFFFFF;
         cursor: pointer;
      }

      button:hover {
         background-color: #716D6E;
         color: white;
         transition: 0.5s;
      }

}


.messangers {
   width: 20%;
   height: 100%;
   position: relative;
   padding-top: 3px;

   .messangers-container {
      align-content: center;
      align-items: center;
      height: auto;
      margin: 0 auto;
      text-align: center;
   }

   .twitter {
      background-color: #36A4CF;
   }

   .dribble {
      background-color: #EA4C89;
   }

   .facebook {
          background-color: #1E73BE;
   }
   
   a {
      cursor: pointer;
      display: inline-block;
      margin: 0px 3px 0px 3px;
      text-align: center;
      text-decoration: none;
      width: 65px;
      height: 55px;
      line-height: 100px;

      .fa-icon {
         font-size: 30px;
         color: white;
         position: relative;
         top: -13px;
   }
}
}
//Input

@media only screen and (max-width: 1300px) {
   .input-center {
      display: none;
   }

   .messangers {
      display: none;
   }

   .input-text {
      width: 100%;
   }
 }