.testemionals-section {
   max-width: 2245px;
   width: 100%;
   height: 826px;
   position: relative;
   box-sizing: border-box;
   padding: 70px 0px 0px 0px;


}

.testemionals-div {
   width: 1140px;
   height: 716px;
   margin: 0 auto;
   position: relative;



}


.testemionals-block-top, .testemionals-block-down {
   display: flex;
   margin-top: 25px;
}




//////////////////////




.testemionals-block {
   background-color: white;
   box-shadow: 5px 20px 40px 0px rgb(0 0 0 / 5%);
   width: 540px;
   height: 310px;
   margin-right: 10px;
   margin-left: 10px;
  


.testemionals-block-div {
   width: 430px;
   height: 200px;
   position: relative;
   margin: 0 auto;
   top: 50px;
 

   .testemionals-block-text-top {
     width: 430px;
     height: 130px;
 

     p {
       font-family: "Poppins", Sans-serif;
       font-size: 24px;
       font-weight: bold;
       line-height: 36px;
       letter-spacing: -0.48px;
       color: #010103;
       margin: 0px 0px 20px 0px;
     }
   }
 }





 .testemionals-block-text-down {
   display: flex;
  
    height: 70px;
    width: 100%;

 
   .testemionals-block-down-img {
     height: 70px;
     width: 70px;
     height: auto;


     img {
       border-radius: 50%;
       width: 100%;
       height: auto;
     }
   }

   .testemionals-block-down-name {

     display: block;
     margin: 0 auto;
     width: 252px;
     box-sizing: border-box;
     align-items: center;
     padding-top: 15px;

     strong {
       display: flex;
       color: #010103;
       font-family: "Poppins", Sans-serif;
       font-size: 18px;
       font-weight: 600;
       line-height: 18px;
       letter-spacing: -0.36px;
       margin-bottom: 0px;
     }

     span {
       font-family: "Rubik", Sans-serif;
       font-size: 16px;
       font-weight: 400;
       letter-spacing: -0.24px;
     }
   }

 }
}


.testemionals-block-down-icon {

   display: flex;
   width: 70px;
   height: 70px;
   justify-content: center;
   align-items: center;


 i {
   font-size: 32px;
   color: #FF4C30; 
  
 }
}

