.faqs-section {
   width: 100%;
   height: auto;
   background-image: url(https://demo.xpeedstudio.com/carrental/home-v4/wp-content/uploads/sites/5/2020/06/accordian_car.png);
   background-position: 0px 70%;
   background-repeat: no-repeat;
   background-size: auto;
   padding: 67px 0px 100px 0px;
}

.faqs-section-div {
   max-width: 1140px;
   min-height: 438px;
   margin: 0 auto;
   position: relative;
   padding: 15px;
}

.faqs-div-container {
   margin: 0 auto;
   max-width: 800px;
   min-height: 438px;
   display: block;
}

.faqs-div-container-top {
   width: 100%;
   height: 50%;
   position: relative;

   text-align: center;

   h3 {
      color: #010103;
      font-family: "Rubik", Sans-serif;
      font-size: 22px;
      font-weight: 500;
      text-transform: uppercase;
      line-height: 27px;
      letter-spacing: -0.66px;

   }

   h2 {
      color: #010103;
      margin: 0px 0px 30px 0px;
    font-family: "Poppins", Sans-serif;
    font-size: 44px;
    font-weight: bold;
    line-height: 52px;
    letter-spacing: -1.32px;
    
   }

   p {
      color: #817F91;
      font-family: "Rubik", Sans-serif;
      font-size: 16px;
      font-weight: 400;
      line-height: 25px;
      letter-spacing: -0.24px;
   }
}

.read-more-container {
   cursor: pointer;
   width: 100%;
   min-height: 68px;

   .more-div-1{
      box-sizing: border-box;
      display: flex;


      span {
         padding-right: 280px;
      }


   }

   .more-div-2 {
      box-sizing: border-box;
      display: flex;

      span {
         padding-right: 360px;
      }


   }

   .more-div-3 {
      box-sizing: border-box;
      display: flex;

      span {
         padding-right: 350px;
      }

  

   }

   button {
      cursor: pointer;
      width: 100%;
      height: 68px;
      border-radius: 5px;
      border: 1px solid #817F91;
      background-color: transparent;
      transition: all .4s ease;
      font-family: "Poppins", Sans-serif;
      font-size: 18px;
      font-weight: 500;
      line-height: 27px;
      letter-spacing: -0.36px;
      color: #010103;
      background-color: #FFFFFF;
      color: #ffffff;
      background-color: #FF4C30;
      border-radius: 3px 3px 3px 3px;
      box-shadow: 0px 10px 15px 0px rgb(255 83 48 / 35%);
      padding: 18px 45px 18px 45px;
   }

   button:hover {
      box-shadow:0px 10px 10px rgba(255, 255, 255, 0.2);
      transform : translateY(-3px);
   }
   
}

.read-text-div {

   p {
      padding-top: 10px;
      padding-bottom: 10px;
      margin: 0 auto;
      justify-content: center;
      width: 700px;
      font-family: "Rubik", Sans-serif;
      font-size: 16px;
      font-weight: 400;
      line-height: 26px;
      letter-spacing: -0.24px;
      color: #817F91;
      
   }
}

.faqs-div-container-down {
   width: 100%;
   height: 50%;
   min-height: auto;
   max-width: auto;
   position: relative;
   counter-reset: myNumber;
}

.read-more-block{
   display: block;
   //  width: 100%;
   // height: auto;
 }

 .read-more-text {
    border: 1px solid black;
    display: block;
    padding: 40px 45px 35px 45px;
    font-family: "Rubik", Sans-serif;
    font-size: 16px;
    font-weight: 400;
    line-height: 26px;
    letter-spacing: -0.24px;
    color: #817F91;
 }

