.team-section {
    width: 100%;
    height: 1326px;
    position: relative;
    background-color: rgba(171, 46, 46, 0);
    padding: 90px 0px 100px 0px;
    box-sizing: border-box;
    display: block;
}

.team-div {
    width: 1140px;
    height: 1136px;
    display: block;
    margin-right: auto;
    margin-left: auto;
    position: relative;
    padding: 15px;
    
}

.team-div-top {
    width: 100%;
    height: 550px;
    position: relative;
    margin-bottom: 30px;
    display: flex;
}


.team-card {
    width: 33.33%;
    height: auto;
    margin: 0px 20px 0px 20px;
    padding: 0px 0px 20px 0px;
    box-shadow: 0px 20px 10px 0px rgb(0 0 0 / 8%);
}

.team-card-img {
    width: 100%;
    height: 416px;
    position: relative;
     img {
         display: block;
         width: 100%;
         height: auto;
     }
}

.overlay {
    display: flex;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    height: 102.2%;
    width: 100%;
    // margin: 30px;
    opacity: 0;
    transition: .5s ease;
    background-color: rgba(255, 76, 48, 0.83);

    a {
        margin-right: 5px;
    }

    .overlay-icon-div {
        width: 333px;
        height: 70px;
        position: relative;
        text-align: center;
        top: 170px;
        align-items: center;
        line-height: 70px;  
    }

    .team-fa {
        margin: 5px;
        font-style: normal;
        font-weight: 400;
        font-variant: normal;
        text-transform: none;
        line-height: 1;
        font-size: 25px;
        color: white;
    }
}


.team-card-img:hover .overlay{
    opacity: 1;
}

.overlay::before {
    position: absolute;
    top: 20px;
    left: 20px;
    right: 20px;
    bottom: 20px;
    border: 1px solid black;
    width: auto;
    height: auto;
    -webkit-transform: scale(2);
    transform: scale(2);
    opacity: 0;
}

