.team-card-info {
    width: 100%;
    height: 90px;
    position: relative;
    top: 25px;

    .team-card-padding {
        padding: 0px 0px 0px 50px;
        width: 100%;

        h2 {
            color: #010103;
            margin: 0px 0px 0px 0px;
            font-family: "Poppins", Sans-serif;
            font-size: 28px;
            font-weight: 700;
            line-height: 42px;
            letter-spacing: -0.42px;
        }
    
        h3 {
            color: #817F91;
            font-family: "Rubik", Sans-serif;
            font-size: 16px;
            font-weight: 400;
            line-height: 24px;
            letter-spacing: -0.24px;
        }
    }
}


.team-div-down {
    position: relative;
    width: 100%;
    height: 550px;
    display: flex;
}