.navbar {
  width: 100%;
  height: 145px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.2rem;
  z-index: 20;
  position: sticky;
  flex-direction: column;
  top: 0;
  background-color: white;
  margin: 0;

}

.nav-container-top {
   display: flex;
   width: 100%;
   max-width: 1140px;
   height: 40px;
   justify-content: right;

   .texts {
    margin-right: 30px;
    display: flex;

    span{
    color: #716D6E;
    margin-right: 15px;
    font-family: "Lato", Sans-serif;
    font-size: 17px;
    font-weight: bold;
    line-height: 2.5em;
      }
 }
}


.nav-container-under {
   display: flex;
   max-width: 1140px;
   width: 100%;
   height: 100px;
   justify-content: space-between;
   align-items: center;
   margin: 0;
   background-color: white;
   top: 0;
  //  border: 1px solid black; 
   transition: 1s;
}

.nav-logo img {
   cursor: pointer;
 }
 
//  .nav-menu {
//    display: flex;
//    list-style: none;
//    text-align: center;
//    margin-right: 2rem;
//  }

 .nav-item-div {
      display: flex;
      list-style: none;
      text-align: center;
      margin-right: 2rem;
 }

 .nav-links {
   text-decoration: none;
   padding: 0.5rem 1rem;
   height: 100%;
   font-size: 16px;
   font-weight: bold;
   font-family: "Lato", Sans-serif;
   font-weight: bold;
   text-transform: capitalize;
   color: #716D6E;
  //  border: 1px solid black;
 }

 .nav-links:hover {
  color: #4d4c4c;
  background-color: #e7e7e7;
  transition: 0.5s;
 }

 
 .nav-item .active {
   color: #4d4c4c;
   background-color: #e7e7e7;
 }


 // Sticky

 .navbar.sticky {
   position: fixed;
   top: 0;
   height: 90px;
   box-shadow: 0 5px 5px 0 rgba(0,0,0,.2);
   animation: navbar;
   animation-duration: 0.5s;
   transition: 0.5s;
 }

 @keyframes navbar {
  from {
    top: 0px;
  }
  to {
    top: 0px;
  }
}

 
 .navbar.sticky .nav-container-top  {
   display: none;

 }

  
