.sign-link {
  position: absolute;
  justify-content: left;
  text-align: left;
  line-height: 25px;
  right: 20px;
 font-size: 18px;
 color: #ffdd40;
 cursor: pointer;
}
 

 .navbar.sticky .nav-logo img {
   width: 70%;
   transition: .2s;
   height: auto;
 }

 .nav-icon {
   display: none;
 }

 .bars-fa {
   color: black;
 }

 .signin, .signup {
   padding: 7px;
   border: 2px solid #ffdd40;
   box-shadow: 0 5px 5px 0 rgba(0,0,0,.2);
   border-radius: 5px;
   position: relative;
   margin-right: 10px;
   text-decoration: none;
   color: #716D6E;
   cursor: pointer;
   font-weight: 700;
   transition: 0.5s;
 }

  .signin:hover {
    background-color: #ffe599;
    color: white;
  }

  .signup:hover {
    background-color: #ffe599;
    color: white;
  }



 /////////////////////////
 @media screen and (max-width: 1140px) {
  .navbar {
    max-width: 1000px;
    max-height: 75px;
    box-sizing: border-box;
    position: relative;
  }

  .right-photo {
    display: none;
  }

  .nav-container-top {
    display: none;
  }

  .nav-logo img{
    width: 70%;
    transition: .2s;
    height: auto;
    margin-left: 70px;
  }

  .nav-menu {
    display: flex;
    flex-direction: column;
    width: 100%;
    position: absolute;
    top: 50px;
    left: -110%;
    opacity: 1 ;
    transition: all 0.5s ease;
 
  }


  .nav-menu.active {
    background-color: white;
    max-width: 1025px;
    height: 300vw;
    position: relative;
    left: -10px;
    top: 50px;
    // opacity: 1;
    transition: all 0.5s ease; 
    position: absolute;
    background: rgba(0,0,0, 0.5);
  }

  .nav-item-div {
    width: 350px;
    height: 100vh;
    margin: 0;
    padding: 0;
    background-color: white;
    display: inline;
  }

  .nav-item {
    border: 1px solid gray;
    display: flex;
    background-color: white;
    width: 100%;
    height: 50px;
    margin-top: 10px;
    text-align: left;
  }

  .nav-item .active {
    color: #4d4c4c; 
    background-color:#e7e7e7 ;
    width: 100%;
    text-align: left;
    height: auto;
    }

  .nav-links {
    width: 100%; 
    height: auto;
    display: table;
  }
  

  .nav-icon {
    display: block;
    position: absolute;
    // top: -25px;
    // transform: translate(10%, 60%);
    text-align: right;
    right: 0px;
    font-size: 50px;
    cursor: pointer;
    color: #ffdd40;

  
  }
  .navbar.sticky {
    width: 100%;
    position: fixed;
    top: 0;
    height: 90px;
    display: none;
    // box-shadow: 0 5px 5px 0 rgba(0,0,0,.2);
  }
}