.partners-section {
   width: 100%;
   background-color: #e7e5e5;
   
   padding: 85px 0px 30px 0px;
   position: relative;
}

.div-section {
   max-width: 1140px;
   margin: 0 auto;

 
}

.info {

   display: block;
   
   h1 {
      text-align: center;
      color: #716D6E;
       margin: 0px 0px 15px 0px;
       font-family: "Lato", Sans-serif;
       font-size: 46px;
       font-weight: 900;
       line-height: 1.1em;
      }
   
      p {
         text-align: center;
         color: #716D6E;
         font-size: 18px;
         font-weight: 400;
         line-height: 1.7em;
         margin: 30px 0px 0px 0px;
      }

      hr {
         width: 136px;
         height: 4px;
         background-color: #FFBF00;
         border: none;
      }
}

.partners-wrapper {
   width: 100%;
   height: 192px;
   display: flex;
   overflow-x: auto;
 

   .item {
      margin-right: 25px;
      min-width: 210px;
      height: 170px;
      cursor: pointer;
      line-height: 60px;
      

      img { 
         margin-top: 60px;
         height: auto;
         opacity: 1;
         transition: all .4s ease;

      }
   }
}

@media (max-width: 767px) {
   
   .partners-section {
      display: none;
   }
}
