.contact-form-section {
   width: 100%;
   position: relative;
   padding: 110px 0px 90px 0px;
   background-image: url(https://demo.xpeedstudio.com/carrental/home-v4/wp-content/uploads/sites/5/2020/07/contact-bg.png);
   background-position: center center;
   background-repeat: no-repeat;
   background-size: auto;
}

.contact-form-div {
   margin: 0 auto;
   width: 1140px;
   height: 614px;
   position: relative;
   display: flex;
   padding: 10px;
}

.contact-form-right {
   width: 50%;
   height: 614px;
}

.contact-text-top {
   width: 100%;
   height: 234px;

   h2 {
      color: #010103;
      margin: 0px 0px 25px 0px;
      font-family: "Poppins", Sans-serif;
      font-size: 44px;
      font-weight: bold;
      line-height: 52px;
      letter-spacing: -1.32px;
   }

   p {
      width: 410px;
      color: #817F91;
      font-family: "Rubik", Sans-serif;
      font-size: 16px;
      font-weight: 400;
      line-height: 25px;
      letter-spacing: -0.24px;
   }
}

.contact-text-down {
   width: 100%;
   height: 137px;

   .contact-down-icons {
      width: 50%;
      height: 137px;
   }

   .contact-icon-block {
      width: 300px;
      padding-bottom: calc(15px/2);
      font-family: "Rubik", Sans-serif;
      font-size: 16px;
      font-weight: 400;
      line-height: 26px;

      i {
         font-size: 22px;
         color: #ffbf00;
      }

      span {
         color: #010103;
         padding-left: 5px;
      }
   }
}


.contact-form-left {
   width: 50%;
   height: 594px;
   padding: 10px;
   display: block;
}

.left-contact-name {
   width: 100%;
   height: 105px;
   margin-bottom: 20px;
   display: block;

   label {
      display: block;
      color: #0F0F20;
      font-family: "Rubik", Sans-serif;
      font-size: 20px;
      font-weight: 500;
      line-height: 19px;
      letter-spacing: -0.64px;
      padding: 0px 0px 10px 0px;
   }

   input {
      width: 500px;
      padding: 19px 30px 19px 30px;
      margin: 0px 0px 10px 0px;
      border-radius: 0px;
      background-color: #F2F2F2;
      border: none;
      width: 100%;
      max-width: 100%;
      padding: 12px;
      height: auto;
      border-width: 1px;
      border-style: solid;
      border-color: #eaeaea;
      border-radius: 2px;
      background: 0 0;
      background-color: #fafafa;
      -webkit-box-shadow: none;
      box-shadow: none;
      -webkit-box-sizing: border-box;
      box-sizing: border-box;
      -webkit-transition: all .2s linear;
      transition: all .2s linear;
      font-size: 14px;
      line-height: 21px;
   }
}

