.contact-form-btn {
   width: 203px;
   height: 62px;
   padding: 19px 25px 19px 25px;
   font-family: "Rubik", Sans-serif;
   font-size: 18px;
   font-weight: 500;
   line-height: 22px;
   letter-spacing: -0.36px;
   color: #ffffff;
   background-color: transparent;
   background-image: linear-gradient(100deg, #FF4C30 0%, #FF6830 100%);
   border-style: none;
   border-radius: 3px 3px 3px 3px;
   box-shadow: 0px 10px 15px 0px rgb(255 83 48 / 35%);
}
