.trio-cars-section {
   width: 100%;
   height: 1201px;
   background-image: url(https://demo.xpeedstudio.com/carrental/onepage-v4/wp-content/uploads/sites/9/2020/08/road_image.png);
   background-position: -200px 335px;
   background-repeat: no-repeat;
   background-size: cover;
   position: relative;
  justify-content: center;
}

.trio-cars-div {
   max-width: 1140px;
   height: 1031px;
   display: flex;
   margin-right: auto;
   margin-left: auto;
   position: relative;
   top: 80px;
   box-sizing: border-box;
   padding: 10px;
   display: block;

 .trio-image {
    width: 100%;
    height: auto;
 }
}

.trio-cars-top {
   width: 100%;
   height: 525px;
   margin-bottom: 20px;
}

.trio-cars-bottom {
   width: 100%;
   height: 466px;
   position: relative;
   display: flex;
}

.trio-cars-bootom-left-info {
   width: 538px;

   h3 {
      color: #010103;
      font-family: "Rubik", Sans-serif;
      font-size: 22px;
      font-weight: 500;
      line-height: 27px;
      letter-spacing: -0.66px;
   }

   h2 {
      width: 538px;
      color: #010103;
      margin: 0px 0px 25px 0px;
      font-family: "Poppins", Sans-serif;
      font-size: 44px;
      font-weight: bold;
      line-height: 52px;
      letter-spacing: -1.32px;
   }

   p {
      width: 538px;
      color: #817F91;
      font-family: "Rubik", Sans-serif;
      font-size: 16px;
      font-weight: 400;
      line-height: 25px;
      letter-spacing: -0.24px;
   }

   button {
      background-image: linear-gradient(to right,#ff4c30,#ff6830,#fff,#f3806f);
      background-size: 300% 100%;
      -o-transition: all .4s ease-in-out;
      -webkit-transition: all .4s ease-in-out;
      transition: all .4s ease-in-out;
      padding: 17px 24px 17px 24px;
    font-family: "Rubik", Sans-serif;
    color: white;
    font-size: 18px;
    font-weight: 500;
    line-height: 22px;
    letter-spacing: -0.36px;
    background-color: transparent;
    background-image: linear-gradient(100deg, #FF4C30 0%, #FF6830 100%);
    border-style: none;
    border-radius: 3px 3px 3px 3px;
    box-shadow: 0px 10px 15px 0px rgb(255 83 48 / 35%);
   }
}





.trio-cars-bottom-right-info {
   width: 581px;
   position: relative;
}

.cars-right-info-block {
   width: 100%;
   height: 142px;
   margin-bottom: 20px;
   position: relative;
   display: flex;
}

.trio-cars-right-block {
   width: 581px;
   padding: 0px 20px 7px 120px;
   display: flex;

   .trio-cars-image-div {
      position: relative;
      margin: 10px;
      z-index: 2;
      width: 102px;
      height: 87px;
      flex: 0 0 auto;

      img {
         width: 100%;
         height: auto;
         transition: transform .7s ease-in-out;
      }
   
      img:hover {
         transform: rotateY(360deg);
      }
   }

   h3 {
      margin: 0px 0px 10px 0px;
    padding: 0px 0px 0px 0px;
    color: #010103;
    font-family: "Poppins", Sans-serif;
    font-size: 24px;
    font-weight: 600;
    text-transform: capitalize;
    line-height: 35px;
    letter-spacing: -0.72px;
   }

   p {
      color: #817F91;
      font-family: "Rubik", Sans-serif;
      font-size: 16px;
      font-weight: 400;
      line-height: 26px;
      letter-spacing: -0.24px;
   }
}


