.quality-section {
   width: 100%;
   display: flex;
   position: relative;
   padding: 150px 0px 150px 0px;
 
}

.div-sec {
   max-width: 1140px;
   display: flex;
   position: relative;
   margin: 0 auto;
   padding: 15px;
}

.left-info {
   max-width: 570px;
   min-width: 300px;
   // height: 360px;
   text-align: left;

   h2 {
      color: #716D6E;
      margin: 0px 0px 33px 0px;
      font-family: "Lato", Sans-serif;
      font-size: 40px;
      font-weight: 900;
      line-height: 1.1em;
      font-family: "Poppins";
   }

   h3 {
      width: 500px;
      font-family: "Poppins";
      color: #716D6E;
      font-size: 30px;
      font-weight: 400;
      line-height: 1.4em;
      margin: 0px 0px 23px 0px;
   }

   p { 
      color: #716D6E;
      font-size: 18px;
      font-weight: 500;
      line-height: 1.7em;
      margin: 0px 0px 10px 0px;
   }
}

.btn-left {
   width: 260px;
   height: 46px;
   color: #FFFFFF;
   font-family: "Lato", Sans-serif;
   font-size: 20px;
   font-weight: 900;
   line-height: 36px;
   border-radius: 0px 0px 0px 0px;
   background-color: #FFBF00;
   box-shadow: 6px 6px 0px 0px #efe9e9;
   border: none;
   transition: 0.3s;
   cursor: pointer;
}

.btn-left:hover {
   background-color: #716D6E;
   transition: 0.3s;
}


.btn-right {
   border: none;
   width: 260px;
   height: 46px;
   margin-left: 15px;
   color: #FFFFFF;
   font-family: "Lato", Sans-serif;
   font-size: 20px;
   font-weight: 900;
   line-height: 36px;
   border-radius: 0px 0px 0px 0px;
   background-color: #716D6E;
   box-shadow: 6px 6px 0px 0px #efe9e9;
   transition: 0.3s;
   cursor: pointer;
}

.btn-right:hover {
   background-color: #FFBF00;
   transition: 0.3s;
}

.right-photo {
   max-width: 570px;
   min-width: 300px;
   height: auto;
   box-shadow: 6px 6px 0px 0px #efe9e9;

   img {
      width: 100%;
      height: auto;
   }
}

////////////////////////////////@at-root

.video-section {
   width: 100%;
   display: flex;
   position: relative;
   padding: 0px 0px 150px 0px;
   
}

.div-sec-2 {
   max-width: 1140px;
   display: flex;
   position: relative;
   margin: 0 auto;
   padding: 15px;

}

.left-video {
   max-width: 660px;
   height: 360px; 
   position: relative;
}


.right-text {
   max-width: 540px;
   height: 360px;
   margin-left: 15px;

   h2 {
      color: #716D6E;
      margin: 0px 0px 33px 0px;
      font-family: "Lato", Sans-serif;
      font-size: 36px;
      font-weight: 900;
      line-height: 1.1em;
   }

   h3 {
      width: 480px;
      color: #716D6E;
      font-size: 26px;
      font-weight: 400;
      line-height: 1.4em;
      margin: 0px 0px 23px 0px;
   }

   p {
      color: #716D6E;
      font-size: 16px;
      font-weight: 400;
