.howitwork-section {
    width: 100%;
   margin: 0 auto;
    height: 554px;
    padding: 100px 0px 25px 0px;
    position: relative;
}

.howitwork-div {
   max-width: 1140px;
   height: 529px;
   margin: 0 auto;
   box-sizing: border-box;
   padding: 10px;
}

.howitwork-top {
    width: 100%;
    max-width: 1120px;
    height: 165px;
    text-align: center;

    h3 {
        color: #010103;
        font-family: "Rubik", Sans-serif;
        font-size: 22px;
        font-weight: 600;
        line-height: 27px;
        letter-spacing: -0.66px;
    }

    h2{
        color: #010103;
        margin: 0px 0px 50px 0px;
        font-family: "Poppins", Sans-serif;
        font-size: 44px;
        font-weight: bold;
        line-height: 60px;
        letter-spacing: -1.32px;
    }
}

.howitwork-bottom {
    width: 100%;
    max-width: 1120px;
    height: 344px;

    margin-right: auto;
    margin-left: auto;
    position: relative;
    display: flex;
}

.howitwork-block {
    width: 33.33%;
    padding: 10px;
    position: relative;
    padding: 0px 30px 0px 30px;
    border-width: 1px 1px 1px 1px;
   
}

.block-image-div {
    width: 100%;
    height: 177px;
    margin: 0 auto;
    position: relative;
    text-align: center;




     img {
      transition: all 0.2s ease;
      /* Градусы поворота можно изменить по необходимости */
      }

      img:hover {
        -webkit-transform: rotate(25deg); /* Градусы поворота можно изменить по необходимости */
          transform: rotate(25deg); /* Градусы поворота можно изменить по необходимости */
         transition: all 0.3s ease;
        }

}


.block-info-div {
    width: 100%;
    height: 110px;
    text-align: center;
    position: relative;
    z-index: 2;
   


    h3 {
    margin: 0px 0px 10px 0px;
    padding: 0px 0px 0px 0px;
    color: #010103;
    font-family: "Poppins", Sans-serif;
    font-size: 26px;
    font-weight: bold;
    text-transform: capitalize;
    letter-spacing: -0.78px;
    }

    p {
    color: #817F91;
    font-family: "Rubik", Sans-serif;
    font-size: 16px;
    font-weight: 400;
    line-height: 23px;
    letter-spacing: -0.24px;
    }
}