.center-sect-2 {
   background: linear-gradient(120deg,#FFBF00, #8b898b);
    max-width: 100%;
    height: 100vh;
 }
 
 .center-2{
    position: absolute;
    top: 55%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 400px;
    background: white;
    border-radius: 10px;
    box-shadow: 10px 10px 15px rgba(0,0,0,0.05);
  }
  .center-2 h1{
    text-align: center;
    padding: 20px 0;
    border-bottom: 1px solid silver;
  }
  .center-2 form{
    padding: 0 40px;
    box-sizing: border-box;
  }
  form .txt_field-2{
    position: relative;
    border-bottom: 2px solid #adadad;
    margin: 30px 0;
  }
  .txt_field-2 input{
    width: 100%;
    padding: 0 5px;
    height: 40px;
    font-size: 16px;
    border: none;
    background: none;
    outline: none;
  }
  .txt_field-2 label{
    position: absolute;
    top: 50%;
    left: 5px;
    color: #adadad;
    transform: translateY(-50%);
    font-size: 16px;
    pointer-events: none;
    transition: .5s;
  }
  .txt_field-2 span::before{
    content: '';
    position: absolute;
    top: 40px;
    left: 0;
    width: 0%;
    height: 2px;
    background: #2691d9;
    transition: .5s;
  }
  .txt_field-2 input:focus ~ label,
  .txt_field-2 input:valid ~ label{
    top: -5px;
    color: #2691d9;
  }
  .txt_field-2 input:focus ~ span::before,
  .txt_field-2 input:valid ~ span::before{
    width: 100%;
  }
  .pass-2{
    margin: -5px 0 20px 5px;
    color: #a6a6a6;
    cursor: pointer;
  }
  .pass-2:hover{
    text-decoration: underline;
  }
  input[type="submit"]{
    width: 100%;
    height: 50px;
    border: 1px solid;
    background: #99ce44;
    border-radius: 25px;
    font-size: 18px;
    color: #e9f4fb;
    font-weight: 700;
    cursor: pointer;
    outline: none;
  }
  input[type="submit"]:hover{
    border-color: #2691d9;
    transition: .5s;
  }
  .signup_link-2{
    margin: 30px 0;
    text-align: center;
    font-size: 16px;
    color: #666666;
  }
  .signup_link-2 a{
    color: #2691d9;
    position: relative;
    left: 120px;
    text-decoration: none;
  }
  .signup_link-2 a:hover{
    text-decoration: underline;
  }