.rent-section {
   width: 100%;
   position: relative;
}


.rent-container {
   max-width: 1500px;
   margin: 0 auto;
   height: auto;
   display: flex;
   position: relative;
   // padding: 15px;
}

.rent-card-block {
   position: relative;
   max-width: 1140px;
   height: auto;
  margin: 0 auto;
}

.rent-filter {
   min-width: 300px;
   height: 600px;
   position: -webkit-sticky;
   position: sticky;
   top: 120px;
   border: 1px solid black;
}



.filter-head {
   // max-width: 300px;
   width: 100%;
   min-height: 100px;
   position: relative;
   border: 1px solid black;
   color: white;
   position: relative;
   height: 80px;
   background-color: #232628;
   display: flex;
   margin: 0 auto;


   .filter-head-icon {
      width: 25%;

      text-align: center;
      line-height: 100px;
      
      i {
         font-size: 28px;
         font-weight: bold;
         color: #fff;
      }
   }
   .filter-head-name {
      width: 75%;
     

      h2 {
         line-height: 30px;
         width: 200px;
         display: block;
         color: #fff;
         font-weight: 700;
         letter-spacing: -0.3px;
      }
   }
}



.filter-search {
   width: 100%;
   height: 70px;
   position: relative;
   border: 1px solid black;
}

.filter-price {
   width: 100%;
   height: 140px;
   position: relative;
   border: 1px solid black;
}

.filter-kuzov {
   width: 100%;
   height: 70px;
   position: relative;
   border: 1px solid black;
}

.filter-marka {
   width: 100%;
   height: 70px;
   position: relative;
   border: 1px solid black;
}

.filter-mesto {
   width: 100%;
   height: 70px;
   position: relative;
   border: 1px solid black;
}

.container-grid {
   display: grid;
   grid-template-columns: repeat(3, 3fr);
   grid-gap: 30px;
 }

 .fetch-input {
       width: 260px;
       height: 40px;
       position: relative;
       top: 115px;
       left: -333px;
       font-size: 18px;
       font-family: "Poppins", Sans-serif;
 }
 
 .car-card {
        transition: box-shadow  0.5s;
         max-width: 350px;
         // height: 530px;
         height: 500px;
         // display: block;
         border-style: solid;
         border-width: 1px 1px 1px 1px;
         border-color: #D5D5D5;
         margin-bottom: 30px;
     

         .car-image-block {
            border-width: 1px 1px 1px 1px;
            border-color: #D5D5D5; 
            width: 350px;
            height: 220px;

            img {
               width: 100%;
               height: 100%;
            }
         }

         .car-info-block {
            width: 100%;
            height: 250px;

            .car-info-head {
               width: 100%;
               height: 80px;
               margin-top: 0px;
               margin-bottom: -10px;
               display: flex;
            }

            .info-head-name {
               padding: 10px 15px 15px 25px;
               display: block;
               width: 72%;
               height: auto;
               

               .info-head-container {
             
                  height: 100%;

                  h2 {
                     color: #010103;
                     margin: 0px 0px 0px 0px;
                     font-family: "Poppins", Sans-serif;
                     font-size: 20px;
                     font-weight: 700;
                     line-height: 35px;
                     letter-spacing: -0.72px;
                  }

                  img {
                     max-width: 100%;
                     height: auto;
                  }
               }
            }

            .info-head-price {
               width: 28%;
               height: auto;
               display: block;
               height: auto;
               padding: 10px 15px 15px 15px;


               .info-head-price-container {
                  height: 100%;
                

                  h2 {
                     text-align: center;
                     margin: -3px 0px 0px 0px;
                     padding: 0px 0px 0px 0px;
                     color: #b9b452;
                     font-family: "Poppins", Sans-serif;
                     font-size: 20px;
                     font-weight: 700;
                     line-height: 48px;
                     letter-spacing: -0.96px;
                  }

                  p {
                     color: #010103;
                    font-family: "Rubik", Sans-serif;
                    font-size: 16px;
                    font-weight: 400;
                    line-height: 5px;
                    letter-spacing: -0.24px;
                    margin: 0px 0px 0px 0px;
                  }
               }
            }
         }
      }

      .car-card:hover {
         box-shadow: 7px 7px 10px rgb(0 0 0 / 0.2);
         transition: box-shadow 0.3s ease-in-out;
       }

      .more-btn {
         cursor: pointer;
         color: white;
         text-decoration: none;
         font-size: 18px;
         font-family: "Rubik", Sans-serif;
         font-weight: 400;
         letter-spacing: -0.27px;
         position: relative;
         left: -8px;
      }


