.more-info-btn-sectioon {
   width: 100%;
   height: auto;
   margin: 0 auto;
   position: relative;
   display: block;
   // margin-top: -10px;
   padding: 0px 0px 0px 0px;
   text-align: center;


   button {
      max-width: 180px;
      height: 56px;
      text-align: center;
      font-family: "Rubik", Sans-serif;
      font-size: 20px;
      font-weight: 500;
      text-transform: capitalize;
      line-height: 22px;
      letter-spacing: -0.16px;
      background-image: linear-gradient(100deg, #FF4C30 0%, #FF6830 100%);
      border-style: none;
      border-radius: 3px 3px 3px 3px;
      box-shadow: 0px 10px 15px 0px rgb(255 83 48 / 35%);
      border-radius: 3px;
      font-size: 20px;
      padding: 15px 30px;
      color: #fff;
      position: relative;
      line-height: 1;
      transition: all .4s ease;

      .fa-check {
         left: 7px;
         top: 3px;
         position: relative;
      }
  }
  
